        props: {
            selectedPattern: {
                type: String,
            },
            bgColour: {
                default: "#000",
            },
            fgColour: {
                default: "#FFF",
            },
            opacity: {
                default: 0.075,
            }
        },
        methods: {
            style: function (pattern, colour) {
                colour = colour || this.bgColour;
                return {
                    backgroundColor: colour,
                    backgroundImage: this.bgPattern(pattern, this.fgColour, this.opacity)
                };
            },
            bgPattern: function bgPattern(pattern, fgColour, opacity) {
                if (!pattern) {
                    return 'none'
                }
                fgColour = fgColour || this.fgColour;
                opacity = opacity || this.opacity;
                var svg = pattern.image.replace('fill="#000"', 'fill="' + fgColour + '" fill-opacity="' + opacity + '"').replace(/\"/g, '\'').replace(/\</g, '%3C').replace(/\>/g, '%3E').replace(/\&/g, '%26').replace(/\#/g, '%23');
                return 'url("data:image/svg+xml,' + svg + '")';
            }
        },
        watch: {
            selectedPattern: {
                initial: true,
                handler: function(newValue) {
                    var matchingPattern = this.patterns.find((pattern) => {
                        return pattern.name === newValue
                    });
                    this.$emit('change:background', this.style(matchingPattern));
                }
            }
        }
    }
